/* fullpage */
#fp-nav.right { right: 44px; transition: all 0.7s; }
#fp-nav ul { display: flex; gap: 4px; flex-direction: column; }
#fp-nav ul li { width: 24px; height: 24px; margin: unset; }
#fp-nav ul li:last-child { display: none; }
#fp-nav ul li a { display: flex; position: unset; justify-content: center; align-items: center; }
#fp-nav ul li a span { background-color: #e4e4e4; width: 8px; height: 8px; position: unset; left: unset; top: unset; margin: unset; }
#fp-nav ul li a.active { display: flex; position: unset; border-radius: 100%; border: 1px solid #09abbe; }
#fp-nav ul li a.active span { background-color: #09abbe; width: 8px; height: 8px; position: unset; left: unset; top: unset; margin: unset; }
#fp-nav ul li:hover a span { margin: unset; width: 8px; height: 8px; }
#fp-nav ul li:hover a.active span { width: 8px; height: 8px; margin: unset; }
.fp-section { display: block; }
.fp-tableCell { display: block; position: relative; }

@media screen and (max-width:768px) {
    #fp-nav.right { right: 20px; top: 145px; }
}
