/*  */
.main .popupWrap { display: none; }
.main .popupWrap.open { display: block; }
.main .popup { 
    position: fixed;
    left: 50%;
    top: calc(100vh / 2);
    transform: translate(-50% , -50%);
    max-width: calc(100% - 40px);
    max-height: calc(100vh - 160px);
    background-color: #333;
    z-index: 9999;
    box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
    /* height: 500px; */
    /* overflow: auto; */
    width: 500px;
}
.main .popup.none { display: none; }
.main .popup .popup-btn-wrap { 
    width: 100%; 
    height: 30px; 
    display: flex; 
    align-items: center; 
    justify-content: space-between; 
    position: absolute;
    bottom: -30px;
    z-index: 9999;
    background: #333;
}
.main .popup .popup-btn-wrap label { display: flex; color: #fff; gap: 8px; padding: 0 8px; }
.main .popup .popup-btn-wrap .close { color: #fff; padding: 0 16px; display: flex; align-items: center; font-size: 14px; background-color: #333; height: 100%; }
.main .popup .img-wrap { cursor: pointer; width: 100%; position: relative; max-height: calc(100vh - 160px); }
.main .popup .img-wrap::before { 
    content: ''; width: 100%; display: block; padding-top: 130%; 
}
.main .popup .img-wrap img { 
    width: 100%; height: 100%; 
    object-position: center; 
    /* object-fit: contain;  */
    position: absolute; 
    left: 0; top: 0; 
}
.main .popup .swiper-pagination-bullet.swiper-pagination-bullet-active { background-color: #fff; }



